* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 14px;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style-type: none;
  display: inline-block;
  padding: 0.5em;
  background-color: #FFF;
  text-align: center;
}

.selected {
  border: 2px solid red;
}

img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  display: block;
}

li:hover {
  cursor: pointer;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

span {
  display: block;
  font-size: 0.8em;
  font-style: italic;
}

.FoodsContainer {
  display: -webkit-flex;
  display: flex;
  overflow: scroll;
  overflow-x: hidden;
  height: calc(100% - 3em);
}

p {
  margin: 1em 0;
  line-height: 1em;
}

.leaflet-container {
  -webkit-flex: 3 1;
          flex: 3 1;
}

main {
  display: -webkit-flex;
  display: flex;
}

aside {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 550px;
}

footer {
  margin-top: 2em;
  text-align: center;
  font-style: italic;
}

